const urlGoogleSheet =
  "https://script.google.com/macros/s/AKfycbx-8xQOPoyCIVN0iT8Q0r7cOZbCLXnyPuIwmcj4dGTGEeY1Wgo8cn_95Yh0GZnXpxTmiA/exec";
const form = document.forms["contact-form"];

document.getElementById("created_at").value = new Date().toLocaleString(
  "pt-BR",
  {
    timeZone: "America/Sao_Paulo",
  }
);

form.addEventListener("submit", function (e) {
  e.preventDefault();
  const submit = document.getElementById("submit");
  submit.setAttribute("disabled", true);

  fetch(urlGoogleSheet, {
    method: "POST",
    body: new FormData(form),
  })
    .then((response) => {
      alert("Dados enviados com sucesso!", response);
      form.reset();
      submit.removeAttribute("disabled");
    })
    .catch((error) => {
      console.error("Erro no envio dos dados!", error);
    });
});
